.set-overview-container {
  display: flex;
  flex-direction: column;

  align-items: center;
}

#sets {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 30px;
}

#set-overview-title {
  font-size: 72px;
  font-weight: 900;
}

.set-container {
  display: flex;
  flex-direction: row;
}

.set-activators-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.set-activator-thresholds {
  display: flex;
  flex-direction: row;
  width: 100%; 
  gap: 8px;
}

.set-threshold {
  padding: 5px 10px;
  border: 1px solid #eee;
}

.set-threshold.active {
  background-color: #151;
}

.left-panel {
  width: 20%;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  gap: 8px;
}

.set-activator {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(25% - 2px);
  height: 45px;
  border: 1px solid #eee;
  font-weight: 100;
}

.set-activator.active {
  background-color: #151;
  font-weight: 400;
}